import React, {ChangeEvent, FC, useEffect} from 'react';
import {Preloader} from '../../common/Preloader/Preloader';
import {useAppDispatch, useAppSelector} from 'redux/store';
import {profileThunks, ProfileType} from 'redux/profile-reducer';
import {ProfileStatus} from './ProfileStatus';
import {useParams} from 'react-router-dom';


const ProfileInfo: FC = () => {
    const profile = useAppSelector<ProfileType | null>(state => state.profilePage.profile);
    const authorizedUserId = useAppSelector(state => state.auth.userId)
    const dispatch = useAppDispatch()

    let {userId} = useParams<{ userId: any }>();
    if (!userId) {
        userId = authorizedUserId
    }

    useEffect(() => {
        dispatch(profileThunks.getUserProfile(userId))
        dispatch(profileThunks.getUserStatus(userId))
    }, []);

    if (!profile) {
        return <Preloader/>
    }
    const onMainPhotoSelected = (e: ChangeEvent<HTMLInputElement>) => {
        if (e.currentTarget.files?.length) {
            dispatch(profileThunks.savePhoto(e.currentTarget.files[0]))
        }
    }

    return (
        <div key={profile.userId}>
            <div>
                <img
                    alt={'image'}
                    src={'data:image/jpeg;base64,'}/>
            </div>
            <div style={{padding: '10px'}}>
                <img src={profile.photos.large } alt="profileInfo"/>
                {userId === authorizedUserId && <input type="file" onChange={onMainPhotoSelected}/>}
                <ProfileStatus/>
                <p>{profile.fullName}</p>
                <p>About me: {profile.aboutMe}</p>
                <div>Contacts:
                    <p> - {profile.contacts.facebook}</p>
                    <p> - {profile.contacts.website}</p>
                    <p> - {profile.contacts.vk}</p>
                    <p> - {profile.contacts.twitter}</p>
                    <p> - {profile.contacts.instagram}</p>
                    <p> - {profile.contacts.youtube}</p>
                    <p> - {profile.contacts.github}</p>
                    <p> - {profile.contacts.mainLink}</p>
                </div>
                <p>
                    Looking for a job? {profile.lookingForAJob ? 'No' :
                    profile.lookingForAJobDescription}
                </p>
            </div>
        </div>
    )
}


export default ProfileInfo